<template>
  <div>
    <div v-if="images" class="tweetImg_wrapper">
      <div class="tweetImg" v-for="(image, index) in images" v-bind:key="index">
        <!-- <p>{{image.name}}</p> -->
        <img v-bind:src="image.thumnail">
        <p><button class="btn btn-primary btn-imgremove" v-on:click="removeImage">Remove</button></p>
      </div><!--tweetImg-->
    </div><!--tweetImg_wrapper-->
  </div>
</template>

<script>
/* eslint-disable no-console */
export default {
  name: 'ImageUploads',
  data() {
    return {
      images: []
    }
  },
  methods: {
    onFileChange(event) {
      let files = event.target.files || event.dataTransfer.files;
      this.createImage(files[0]);//
    },
    createImage(file) {
      let reader = new FileReader();
      let that = this;
      let obj = {};
      reader.onload = function(e) {
        obj.thumnail = e.target.result;//
        obj.uploadFile = file;//
        obj.name = file.name;//
        that.images.push(obj);//
      }
      reader.readAsDataURL(file);
    },
    removeImage(index) {
      let that = this;
      that.images.splice(index, 1)//index1
    }
  }
}
</script>

