<template>
  <div class="Success">
    <h1></h1>
    <h2>!:{{ useremail }}</h2>
   <button @click="signOut"></button>
  </div>
</template>

<script>
import firebase from 'firebase/app'
import "firebase/auth"
export default {
  name: 'Success',
  data () {
    return {
      useremail: firebase.auth().currentUser.email
    }
  },
  methods:{
    signOut: function() {
      firebase.auth().signOut().then(() => {
        this.$router.push('/login')
      })
    }
  }
}
</script>
