<template>
  <div class="signup">
    <h1></h1>
    <input type="mail" placeholder="" v-model="mail">
    <input type="password" placeholder="" v-model="password">
    <button v-on:click="signUp"></button>
    <p>
      <router-link to="/login"></router-link>
    </p>
  </div>
</template>


<script>
/* eslint-disable no-console */
// @ is an alias to /src
import firebase from 'firebase/app'
import "firebase/auth"
export default {
  name: 'SignUp',
  data() {
    return {
      mail: '',
      password: ''
    }
  },
  methods: {
    signUp: function () {
      firebase.auth().createUserWithEmailAndPassword(this.mail, this.password)
        .then(user => {
          alert('', user.email)
          this.$router.push('/login')
        })
        .catch(error => {
          alert(error.message)
        })
    }
  }
}
</script>
