      this.uploadedImage = []
      const files = elem.target.files || elem.dataTransfer.files
      for (var i = 0; i < files.length; i++) {
        let file = files[i]
        let url = URL.createObjectURL(file)
        console.log(url)
        this.uploadedImageURL.push({ src: url })
        this.uploadedImage.push( file )
      }
    },
