      // console.log(this.uploadedImageURL[0]);
      let formData = new FormData();
      formData.append("uploadFile", this.uploadedImage[0]);
      let config = {
        headers: {
          "content-type": "multipart/form-data"
        }
      };
      let vm = this
      axios
          .post('./upload.php', formData, config)
          .then(function(response) {
              vm.response = response.data;
              // response 
              // vm.uploadFile = response.data.uploadFile;
              console.log(response.data);
          })
